import { Validators } from "@electra/utility";
import { AbstractColumn } from "../AbstractColumn";
import { IndexDefinition } from "../../Definition/IndexDefinition";
import { ColumnDefinition } from "../../Definition/ColumnDefinition";
export class DateColumn extends AbstractColumn {
    constructor(name, options) {
        super(name);
        this.options = Object.assign({ nullable: false, default: undefined, index: false, addAfter: undefined }, options);
        this.validateOptions(this.options, {
            nullable: Validators.boolean(),
            default: Validators.regex(/^\d{4}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01])$/, 'YYYY-MM-DD', { optional: true }),
            index: Validators.boolean(),
            addAfter: Validators.string({ optional: true })
        });
    }
    getColumnDefinition() {
        return ColumnDefinition
            .create(this.name, "DATE")
            .nullable(this.options.nullable)
            .default(this.options.default ? `'${this.options.default}'` : undefined)
            .after(this.options.addAfter);
    }
    getIndexDefinition() {
        if (!this.options.index)
            return null;
        return IndexDefinition
            .create()
            .columns(this.name);
    }
}
