export class ColumnDefinition {
    constructor(name, type) {
        this.options = {
            nullable: undefined,
            default: undefined,
            unsigned: undefined,
            autoIncrement: undefined,
            zeroFill: undefined,
            primaryKey: undefined,
            after: undefined
        };
        this.name = name;
        this.type = type;
    }
    static create(name, type) {
        return new ColumnDefinition(name, type);
    }
    nullable(nullable) {
        this.options.nullable = nullable;
        return this;
    }
    default(value) {
        this.options.default = value;
        return this;
    }
    unsigned(unsigned) {
        this.options.unsigned = unsigned;
        return this;
    }
    autoIncrement(autoIncrement) {
        this.options.autoIncrement = autoIncrement;
        return this;
    }
    zeroFill(zerofill) {
        this.options.zeroFill = zerofill;
        return this;
    }
    primaryKey(primaryKey) {
        this.options.primaryKey = primaryKey;
        return this;
    }
    after(after) {
        this.options.after = after;
        return this;
    }
    get() {
        let definition = `\`${this.name}\` ${this.type}`;
        if (this.options.unsigned === true)
            definition += " UNSIGNED";
        if (typeof this.options.nullable === "boolean")
            definition += this.options.nullable ? " NULL" : " NOT NULL";
        if (this.options.default !== undefined)
            definition += ` DEFAULT ${this.options.default}`;
        if (this.options.autoIncrement === true)
            definition += " AUTO_INCREMENT";
        if (this.options.zeroFill === true)
            definition += " ZEROFILL";
        if (this.options.primaryKey === true)
            definition += " PRIMARY KEY";
        if (typeof this.options.after === "string")
            definition += ` AFTER \`${this.options.after}\``;
        return definition;
    }
}
